import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		guests: [],
		roles: [
			{ id: 1, name: 'stuff' },
			{ id: 2, name: 'pressa' },
			{ id: 3, name: 'speaker' },
			{ id: 4, name: 'customer' },
			{ id: 5, name: 'subscriber' },
			{ id: 6, name: 'other' }
		],
		statuses: [
			{ id: 1, name: 'common' },
			{ id: 2, name: 'vip' },
			{ id: 3, name: 'super vip' }
		],
		currentEvent: {
			startDate: '2019-07-08',
			endDate: '2019-07-15'
		},

		isDisable: false
	},

	mutations: {
		PENDING(state) {
			state.isDisable = true;
		},
		FAILURE(state) {
			state.isDisable = false;
		},
		addGuests(state, payload) {
			for (const key in payload) {
				state.guests.push(payload[key]);
			}
		}
	},

	actions: {
		// Create
		async createGuest({ commit }, guest) {
			commit('PENDING');
			await axios.post('https://elementtable-78c2d.firebaseio.com/results.json', guest);
			return response;
		},

		// Read
		readGests({ commit }) {
			axios
				.get('https://elementtable-78c2d.firebaseio.com/results.json')
				.then(response => {
					// handle success
					commit('addGuests', response.data);
				})
				.catch(function(error) {
					// handle error
					console.log(error);
				})
				.finally(function() {
					// always executed
				});
		}
	},

	getters: {
		// flatten guests object for table
		getGests(state) {
			const processedgGuests = state.guests.map(guest => {
				return {
					check_in_date: guest.check_in_date,
					check_in_time: guest.check_in_time,
					check_out_date: guest.check_out_date,
					check_out_time: guest.check_out_time,
					distance_to_event: guest.distance_to_event,
					event: guest.event,
					email: guest.guest.email,
					gender: guest.guest.gender,
					guestId: guest.guest.id,
					name: guest.guest.name,
					phone: guest.guest.phone,
					id: guest.id,
					max_payment: guest.max_payment,
					plus_one: guest.plus_one,
					ready_to_share_accommodation: guest.ready_to_share_accommodation,
					ready_to_share_room: guest.ready_to_share_room,
					role: guest.role,
					same_gender_sharing: guest.same_gender_sharing,
					status: guest.status
				};
			});
			return processedgGuests;
		},

		// flatten guests object for forms
		emptyGuest(state) {
			return {
				check_in_date: '',
				check_in_time: '',
				check_out_date: '',
				check_out_time: '',
				distance_to_event: null,
				event: null,
				email: '',
				gender: '',
				guestId: null,
				name: '',
				phone: '',
				id: null,
				max_payment: null,
				plus_one: false,
				ready_to_share_accommodation: false,
				ready_to_share_room: false,
				role: null,
				same_gender_sharing: true,
				status: null
			};
		},

		getIsDisable(state) {
			return state.isDisable;
		}
	}
});
